.todo_list__task {
  border-radius: 6px;
  border: 1px solid #989898;
  display: flex;
  overflow: hidden;
  margin-bottom: 8px;
  transition: all 0.3s;
}

.todo_list__input {
  padding: 12px;
  border: none;
  flex-grow: 1;
  outline: none;
  background: none;
  width: 369px;
}

.todo_list__add_button,
.todo_list__delete_button,
.todo_list__checkbox {
  border: none;
  background: none;
  cursor: pointer;
  margin: 12px;
  width: 20px;
  height: 20px;
}

.todo_list__delete_button_icon {
  transform: rotate(45deg);
}

.todo_list__checkbox {
  min-width: 21px;
  height: 21px;
  border-radius: 50%;
  border: 1px solid #998899;
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  cursor: pointer;
  position: relative;
}

.todo_list__checkbox:checked:before {
  background-color: #C4C4C4;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  position: absolute;
  content: '';
  bottom: 2px;
  right: 2px;
}

.done {
  background: #F4F4F4;
}